<template lang="html">
  <div>

    <div class="mv-highlight">
      <div class="mv-highlight__image" :style="{ backgroundImage: 'url(' + highlightMovue.posterImage + ')' }"></div>
      <div class="mv-highlight__overlay"></div>
      <div class="mv-highlight__overlay mv-highlight__overlay--t-b"></div>

      <div class="mv-highlight__top">

        <div class="mv-highlight__meta">
          <div class="mv-highlight__meta__left">
            <p>2013</p>
            <img src="../assets/media/vector/icons/seperator.svg" alt="" class="mv-highlight__score__icon">
            <p>{{ highlightMovue.genres }}</p>
          </div>

          <div class="mv-highlight__score">
            <img src="../assets/media/vector/icons/fire.svg" alt="" class="mv-highlight__score__icon">
            <span class="mv-highlight__score__current">8.3</span>
            <div class="mv-highlight__score__seperator">/</div>
            <div class="mv-highlight__score__total">10</div>
          </div>
        </div>

        <div class="mv-highlight__user">
          <div class="mv-highlight__user__title">
            <img src="https://avatars1.githubusercontent.com/u/8048514?s=460&v=4" alt="">
            <p>Martijn</p>
            <span>âŒƒ</span>
          </div>
          <div class="mv-highlight__user__dropdown">
            <a @click="onLogout" v-if="userIsAuthenticated" class="main-nav__link">Logout</a>
          </div>
        </div>

      </div>


      <div class="mv-highlight__middle">
        <h1> {{ highlightMovue.title }} </h1>
      </div>


      <div class="mv-highlight__bottom">

        <router-link :to="'/movues/' + highlightMovue.id" router>
        <button class="btn btn--secondary btn--icon">
          <img src="../assets/media/vector/icons/camera.svg" alt="">
          Show movue
        </button>
        </router-link>

        <div class="mv-highlight__slider"></div>

      </div>
    </div>



    <div class="mv-overview__wrapper">

      <h2 class="border-title">Recently movues</h2>

      <div class="movues-container">
        <article
          class="movue-card"
          v-for="movue in movues"
          :key="movue.id"
          :style="{ backgroundImage: 'url(' + movue.posterImage + ')' }">

          <router-link :to="'/movues/' + movue.id" router>
            <section class="movue-card__top">
              {{ movue.genres }}
            </section>

            <section class="movue-card__bottom">
              <h3 class="movue-card__title"> {{ movue.title }} </h3>
            </section>
          </router-link>
        </article>
      </div>

      <div v-if="actionMovues.length > 0" class="actionsMovues">
        <h2 class="border-title">Action movues</h2>

        <div class="movues-container">
          <article
            class="movue-card"
            v-for="movue in actionMovues"
            :key="movue.id"
            :style="{ backgroundImage: 'url(' + movue.posterImage + ')' }">

            <router-link :to="'/movues/' + movue.id" router>
              <section class="movue-card__top">
                {{ movue.genres }}
              </section>

              <section class="movue-card__bottom">
                <h3 class="movue-card__title"> {{ movue.title }} </h3>
              </section>
            </router-link>
          </article>
        </div>
      </div>


      <div v-if="comedyMovues.length > 0" class="comedyMovues">
        <h2 class="border-title">Comedy movues</h2>

        <div class="movues-container">
          <article
            class="movue-card"
            v-for="movue in comedyMovues"
            :key="movue.id"
            :style="{ backgroundImage: 'url(' + movue.posterImage + ')' }">

            <router-link :to="'/movues/' + movue.id" router>
              <section class="movue-card__top">
                {{ movue.genres }}
              </section>

              <section class="movue-card__bottom">
                <h3 class="movue-card__title"> {{ movue.title }} </h3>
              </section>
            </router-link>
          </article>
        </div>
      </div>


      <div v-if="horrorMovues.length > 0" class="horrorMovues">
        <h2 class="border-title">Horror movues</h2>

        <div class="movues-container">
          <article
            class="movue-card"
            v-for="movue in horrorMovues"
            :key="movue.id"
            :style="{ backgroundImage: 'url(' + movue.posterImage + ')' }">

            <router-link :to="'/movues/' + movue.id" router>
              <section class="movue-card__top">
                {{ movue.genres }}
              </section>

              <section class="movue-card__bottom">
                <h3 class="movue-card__title"> {{ movue.title }} </h3>
              </section>
            </router-link>
          </article>
        </div>
      </div>


      <div v-if="dramaMovues.length > 0" class="dramaMovues">
        <h2 class="border-title">Drama movues</h2>

        <div class="movues-container">
          <article
            class="movue-card"
            v-for="movue in dramaMovues"
            :key="movue.id"
            :style="{ backgroundImage: 'url(' + movue.posterImage + ')' }">

            <router-link :to="'/movues/' + movue.id" router>
              <section class="movue-card__top">
                {{ movue.genres }}
              </section>

              <section class="movue-card__bottom">
                <h3 class="movue-card__title"> {{ movue.title }} </h3>
              </section>
            </router-link>
          </article>
        </div>
      </div>


      <div v-if="historicalMovues.length > 0" class="historicalMovues">
        <h2 class="border-title">Historical movues</h2>

        <div class="movues-container">
          <article
            class="movue-card"
            v-for="movue in historicalMovues"
            :key="movue.id"
            :style="{ backgroundImage: 'url(' + movue.posterImage + ')' }">

            <router-link :to="'/movues/' + movue.id" router>
              <section class="movue-card__top">
                {{ movue.genres }}
              </section>

              <section class="movue-card__bottom">
                <h3 class="movue-card__title"> {{ movue.title }} </h3>
              </section>
            </router-link>
          </article>
        </div>
      </div>



    </div>

  </div>
</template>

<script>
export default {
  data () {
    return {
      displayActionMovues: false,
      profileExpand: false
    }
  },
  computed: {
    movues () {
      var slice = this.$store.getters.loadedMovues.reverse().slice(0, 10);
      return slice
    },
    actionMovues () {
      var allMovues = this.$store.getters.loadedMovues
      var actionMovues = allMovues.filter(movue => (movue.genres.toLowerCase() === "action"));
      return actionMovues.slice(0, 10);
    },
    comedyMovues () {
      var allMovues = this.$store.getters.loadedMovues
      var comedyMovues = allMovues.filter(movue => (movue.genres.toLowerCase() === "comedy"));
      return comedyMovues.slice(0, 10);
    },
    horrorMovues () {
      var allMovues = this.$store.getters.loadedMovues
      var horrorMovues = allMovues.filter(movue => (movue.genres.toLowerCase() === "horror"));
      return horrorMovues.slice(0, 10);
    },
    dramaMovues () {
      var allMovues = this.$store.getters.loadedMovues
      var dramaMovues = allMovues.filter(movue => (movue.genres.toLowerCase() === "drama"));
      return dramaMovues.slice(0, 10);
    },
    historicalMovues () {
      var allMovues = this.$store.getters.loadedMovues
      var historicalMovues = allMovues.filter(movue => (movue.genres.toLowerCase() === "historical"));
      return historicalMovues.slice(0, 10);
    },
    highlightMovue () {
      var allMovues = this.$store.getters.loadedMovues
      var highlightedMovue = allMovues.filter(movue => (movue.highlight === "yes"));
      return highlightedMovue[highlightedMovue.length-1]
      console.log(highlightedMovue);
      console.log('testing');
    },
    userIsAuthenticated () {
      return this.$store.getters.user !== null && this.$store.getters.user !== undefined
    }
  },
  methods: {
    onLoadMovue (id) {
      this.$router.push('./movues' + id)
    },
    onLogout () {
      this.$store.dispatch('logout')
    }
  }
}
</script>

<style lang="scss">

@import '../styles/2.settings/index-sett.scss';

.mv-highlight{
  $wrapper-size: 55px;
  $highlight-size: 550px;

  position: relative;
  width: 100%;
  height: $highlight-size;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  &__overlay{
    background: linear-gradient(to right, rgba(#000, 0.5), rgba(#000, 0));
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: -1;

    &--t-b{
      background: linear-gradient(to bottom, rgba(#000, 0.2), rgba(#000, 0));
    }
  }

  &__image{
    position: absolute;
    background-size: cover;
    width: 100%;
    height: 100%;
    z-index: -1;
  }

  &__top{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding-top: $wrapper-size;
    padding-left: $wrapper-size;
    padding-right: $wrapper-size;
  }

  &__meta{

    &__left{
      display: flex;
      align-items: center;

      img{
        margin: 0 5px;
      }

      p{
        margin: 0;
      }
    }
  }

  &__seperator{

  }

  &__score{
    display: flex;
    font-family: $font-header;
    font-size: 2.5rem;
    display: flex;
    align-items: center;

    &__icon{
      margin-right: 5px;
      transform: scale(.9);
    }

    &__current{
      color: $color-main;
    }

    &__seperator{
      font-size: 2rem;
      margin: 0 5px;
    }

    &__total{
      font-size: 1.5rem;
      padding-bottom: 4px;
    }
  }

  &__user{
    transition: $transition1;

    &__title{
      display: flex;
      align-items: center;
      transition: $transition1;
    }

    &__dropdown{
      opacity: 0;
      visibility: hidden;
      transition: $transition1;
    }

    img{
      width: 32px;
      height: 32px;
      border-radius: 50%;
      margin-right: 7px;
    }

    p{
      margin: 0;
      margin-right: 5px;
      font-size: 1.6rem;
    }

    span{
      transform: rotate(180deg);
      margin-bottom: 5px;
    }

    &:hover{
      background: #fff;

      .mv-highlight__user__title{
        color: $color-main;
      }

      .mv-highlight__user__dropdown{
        visibility: visible;
        opacity: 1;
        background: #fff;
      }
    }
  }

  &__middle{
    padding-left: $wrapper-size;

    h1{
      font-size: 6rem;
      max-width: 400px;
    }

    padding-bottom: 30px;
  }

  &__fav{
    display: flex;

    img{
      margin-right: 8px;
      transform: scale(.9);
    }
  }

  &__bottom{
    padding-left: $wrapper-size;
    position: relative;
    margin-bottom: -20px;
  }

  &__cta{

  }

  &__slider{

  }
}

.mv-overview__wrapper{
  padding: 50px;
}

.border-title{
  color: #fff;
  font-size: 2.1rem;
  font-family: $font-body;
  font-weight: 600;
  display: inline-block;
  padding-bottom: 4px;
  margin-bottom: 15px;

  &:after{
    content: '';
    height: 1.5px;
    background: $color-main;
    display: block;
    margin-top: 4px;
    width: 80%;
  }
}

.movues-container{
  display: flex;
  overflow-x: scroll;
  overflow-y: hidden;
  padding: 10px;
  margin-bottom: 30px;
}

.movue-card{
  min-width: 175px;
  max-width: 175px;
  height: 270px;
  background-size: cover;
  color: #fff;
  border-radius: 4px;
  background-position: center;
  margin-right: 30px;
  margin-bottom: 20px;
  transition: $transition1;

  &:hover{
    transform: scale(1.03);
    border-radius: 0;
    box-shadow: 2px 2px 27px 3px rgba(255, 255, 255, 0.1803921568627451);
  }

  &:last-child{
    margin-right: 0;
  }

  a{
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    width: 100%;
    height: 100%;
    color: #fff;
  }

  &__top{
    background: linear-gradient(rgba(#000, 0.4), rgba(#000, 0));
    padding: 15px;
    font-size: 1.7rem;
    text-transform: capitalize;
  }

  &__seperator{
    border-radius: 50%;
    border: 1px solid #fff;
    display: inline-block;
    width: 10px;
    height: 10px;
    margin: 0 5px;
  }

  &__bottom{
    background: linear-gradient(rgba(#000, 0), rgba(#000, .5));
    padding: 15px;
  }

  &__title{
    color: #fff;
    font-size: 2.6rem;
    margin: 0;
    max-width: 120px;
  }
}
</style>



